.Search-container {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 16px;
    align-items: center;
    justify-items: center;

    font-size: 1rem;
}

.Search {
    height: max-content;
    width: 400px;
    padding: 7px 0;

    border: 0;
    border-bottom: 2px solid;
    border-image-slice: 1;
    outline: 0;

    background: transparent;
    transition: 0.4s;

    font-family: inherit;
    font-size: 1.3rem;
    color: white;
}

.Search:focus, :hover {
    border-image: linear-gradient(66deg, seagreen, darkslategrey) 1;
}

.Search-history {
    width: 100%;
    display: flex;
    flex-direction: column;

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.Search-history ul {
    list-style: circle;
    margin-block: 0;
    padding: 0;
}
